import React, { useEffect, useState } from 'react'

import { MotionAnimate } from 'react-motion-animate'

import Tooltip from 'components/Tooltip'

import SenspaceShip from 'tooltips/SenspaceShip'
import { shuffleCouple } from 'helpers/index'
import { fadeInLeft } from 'consts/animation'

import em1 from 'assets/images/em1.png'
import em2 from 'assets/images/em2.png'
import em3 from 'assets/images/em3.png'
import em4 from 'assets/images/em4.png'
import em5 from 'assets/images/em5.png'
import em6 from 'assets/images/em6.png'
import em7 from 'assets/images/em7.png'
import em8 from 'assets/images/em8.png'
import em9 from 'assets/images/em9.png'

import './styles.scss'

const clanImages = [
  em1, em2, em3,
  em4, em5, em6,
  em7, em8, em9,
]

const Clan = () => {
  const [images, setImages] = useState(clanImages)

  useEffect(() => {
    setInterval(() => {
      setImages([...shuffleCouple(images)])
    }, 850)
  }, [])

  return (
    <div className="container clan">
      <div className="clan__inner">
        <MotionAnimate animation="fadeInUp" speed="0.4">
          <div className="content">
            <h2 className="clan__title-mobile">EMBRY CLAN</h2>
          </div>
        </MotionAnimate>
        <MotionAnimate variant={fadeInLeft} speed="0.4">
          <div className="clan__grid">
            {images.map((item, index) => (
              <img key={index} src={item} alt="" />
            ))}
          </div>
        </MotionAnimate>
        <div className="content clan__content">
          <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
            <h2 className="clan__content-title">EMBRY CLAN</h2>
          </MotionAnimate>
          <MotionAnimate animation="fadeInUp" delay={0.75} speed="0.4">
            <p>
              The Embry clan is a set of 8,820 unique NFTs of three types:<br />
              Boy sperm, Girl egg and Androgynous. All of them are freedom-loving and have the property of absorbing glances. They love to smoke illegal worldwide <Tooltip placement="topLeft" overlay={SenspaceShip}>Senspace-ship*</Tooltip> and are crazy about sex.
            </p>
          </MotionAnimate>
        </div>
      </div>
    </div>
  )
}

export default Clan
